                        :value="item.value">
                    </el-option>
                </el-select>
            </p>
            <p>
                案件来源
                <el-select v-model="mutvalue" multiple placeholder="请选择">
                    <el-option
                        v-for="item in wszzdw"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </p>
            <p>
                <span class="demonstration">默认</span>
