<script>
export default {
    data() {
        return {
            options: [{
                    value: '选项1',
                    label: '黄金糕'
                }, {
                    value: '选项2',
                    label: '双皮奶'
                }, {
                    value: '选项3',
                    label: '蚵仔煎'
                }, {
                    value: '选项4',
                    label: '龙须面'
                }, {
                    value: '选项5',
                    label: '北京烤鸭'
                }],
            sinvalue: '',
            mutvalue: '',
            date1:'',
            drawer: false,
            input: "",
            ph:"搜索内容",
            count: 10,
            loading: false,
            url:"/s",
        }
    },
    props: {
        wszzdw:[],
        passages:[],
        select:[],
    },
    mounted:function(){
        console.log('mounted')

        var url='lawinit/'
        var req = new XMLHttpRequest();
        req.open('GET', url, true);
        req.setRequestHeader("Content-type","application/json");
        req.send();
        req.onreadystatechange = function () {
            console.log('init response')
            if (req.readyState == 4 && req.status == 200) {
                var json = req.responseText;
                console.log(typeof(json));
                json = JSON.parse(json)
                console.log(typeof(json));
                var i = 0
                for (var key in json) {
                    console.log(key);     //获取key值
                    console.log(json[key]); //获取对应的value值
                    var oplist = []
                    for (var value in json[key]){
                        oplist.push({'value':value,'label':value})
                    }
                    var n = this.select[i].push(oplist)
                    i+=1
                    console.log(n);
                }
                this.wszzdw=this.select[0]
            }
        };
    },
    computed: {
        noMore () {
            return this.count >= 100
        },
        disabled () {
            return this.loading || this.noMore
        },
        p_num(){
            return this.passages.length
        }
    },
    methods:{
        Search:function () {
            var url='test/'
            console.log('Search for kw:' + this.input )
            var req = new XMLHttpRequest();
            req.open('POST', url, true);
            req.setRequestHeader("Content-type","application/json");
            var obj = { kw : this.input, age: 18 };
            console.log(JSON.stringify(obj))
            req.send(JSON.stringify(obj));

            req.onreadystatechange = function () {
                console.log('response')

                if (req.readyState == 4 && req.status == 200) {
                    var json = req.responseText;
                    console.log(json);
                }
            };
        },
        load () {
            console.log('load',this.count)
            this.loading = true
            this.passages.push('a','b','c',
            'a','b','c',
            'a','b','c',
            'a','b','c',
            'a','b','c',
            )
            setTimeout(() => {
                this.count += 10
                this.loading = false
            }, 500)
        }
    }
}
</script>

